<template>
  <div class="result"
      :class="{'result-good': getData.status === 2,
              'result-low': getData.status === 0,
              'result-bad': getData.status === 1}"
  >
    <div class="icon"
        :class="getData.status === 2 ? 'icon-good' : 'icon-bad'"
    />
    <div class="temp">
      {{temp}}&nbsp;<span class="temp_deg">&#176;C</span>
    </div>
    <div class="text">
      {{getData.text}}
    </div>
  </div>
</template>

<script>
import { RESULT_TEXT } from '../const/resultText'
import tempStatus from '../libs/tempStatus'

export default {
  name: 'Result',
  props: {
    temp: {
      type: Number
    }
  },
  computed: {
    getData: function () {
      const status = tempStatus(this.temp)
      switch (status) {
        case 0: return {
          status,
          text: RESULT_TEXT.LOW
        }
        case 1: return {
          status,
          text:RESULT_TEXT.BAD
        }
        default: return {
          status,
          text: RESULT_TEXT.GOOD
        }
      }
    }
  }
}
</script>

