import ApiData from '../../api/api'
import tempStatus from '../../libs/tempStatus'
// import getRandomFloat from '../../libs/getRandomFloat'

const api = new ApiData('http://localhost:3000/api/') //'https://testtempserv.herokuapp.com/api/

export default function () {
  return store => {
    let dispatch = store.dispatch
    let getter = store.getters
    let timerGoVideo = null
    store.subscribe((mutation) => {
      let payload = mutation.payload
      switch (mutation.type) {
        case 'app/SET_CHECK_TEMP':
          api.call('getTemp', {}, getter['app/getIsAuth'])
          .then(res => {
            if (res.error) {
              throw new Error(res.error)
            }
            setTimeout(() => {
              dispatch('app/setTemp', {
                meta: payload.meta,
                data: res.data
              })
              dispatch('app/setShowVideo', {
                meta: payload.meta,
                data: false
              })
            }, 1000)
          })
          .catch(error => console.log(error))
          break
        case 'app/SET_PHOTO':
          if (!payload.data) return
          dispatch('app/setCheckTemp', {
            meta: payload.meta
          })
          break
          case 'app/SET_TEMP':
            if (payload.data) {
              const status = tempStatus(payload.data)
              const meta = payload.meta
              api.call('data', {
                photo: getter['app/getPhoto'],
                status,
                temp: getter['app/getTemp']
              }, getter['app/getIsAuth']).then(res => {
                console.log(res)
                if (res.error) {
                  throw new Error(res.error)
                }
                dispatch('app/setPhoto', {
                  meta: payload.meta,
                  data: null
                })
              }).catch(e => console.log(e))
              timerGoVideo = setTimeout(() => {
                dispatch('app/setShowVideo', {
                  meta,
                  data: true
                })
                dispatch('app/setTemp', {
                  meta,
                  data: null
                })
              }, 5000)
            }
            break
            case 'app/SET_SHOW_VIDEO':
              if (payload.data) {
                dispatch('app/setTemp', {
                  meta: payload.meta,
                  data: null
                })
                if (timerGoVideo) {
                  clearTimeout(timerGoVideo)
                  timerGoVideo = null
                }
              }
              break
            case 'login/SET_AUTH':
              dispatch('app/setIsLoading', {
                meta: payload.meta,
                data: true
              })
              api.call('auth', {
                email: getter['login/getLogin'],
                password: getter['login/getPassword']
              })
              .then(res => {
                if (res.error) {
                  throw new Error(res.error)
                }
                setTimeout(() => {
                  dispatch('app/setIsLoading', {
                    meta: payload.meta,
                    data: false
                  })
                  dispatch('app/setIsAuth', {
                    meta: payload.meta,
                    data: res.token
                  })
                  dispatch('login/setLogin', {
                    meta: payload.meta,
                    data: ''
                  })
                  dispatch('login/setPassword', {
                    meta: payload.meta,
                    data: ''
                  })
                }, 2000)
              })
              .catch(error => {
                setTimeout(() => {
                  dispatch('app/setIsLoading', {
                    meta: payload.meta,
                    data: false
                  })
                  dispatch('login/setLogin', {
                    meta: payload.meta,
                    data: ''
                  })
                  dispatch('login/setPassword', {
                    meta: payload.meta,
                    data: ''
                  })
                  console.log(error)
                }, 2000)
              })
            break
      }
    })
  }
}
