<template>
  <div id="app">
    <Video v-if="getIsAuth && !getIsLoading"/>
    <Auth v-else-if="!getIsAuth && !getIsLoading"/>
    <div class="spinner" v-else>
      <p class="spinner_text">
        Loading
      </p>
      <pulse-loader class="spinner_dot" :loading="getIsLoading" :color="'#a46bfe'" :size="'15px'"></pulse-loader>
    </div>
  </div>
</template>

<script>
import Video from './components/Video.vue'
import Auth from './components/Auth'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'App',
  components: {
    Video,
    Auth,
    PulseLoader
  },
  computed: {
    ...mapGetters('app', [
      'getIsAuth',
      'getIsLoading'
    ]),
    isAuth: function () {
      return this.getIsAuth
    }
  }
}
</script>

<style lang="scss">
@import './assets/app.scss';
.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  &_text {
    text-shadow: 2px 1px 2px #000000a1;
    font-size: 35px;
    color: #FFFFFF;
  }
  &_dot {
    margin-left: 25%;
  }
}
</style>
