export default {
  namespaced: true,
  state: {
    photo: null,
    temp: null,
    showVideo: true,
    checkTemp: null,
    isAuth: false,
    isLoading: false
  },
  actions: {
    setIsLoading: ({ commit }, payload) => commit('SET_IS_LOADING', payload),
    setIsAuth: ({ commit }, payload) => commit('SET_IS_AUTH', payload),
    setCheckTemp: ({ commit }, payload) => commit('SET_CHECK_TEMP', payload),
    setPhoto: ({ commit }, payload) => commit('SET_PHOTO', payload),
    setTemp: ({ commit }, payload) => commit('SET_TEMP', payload),
    setShowVideo: ({ commit }, payload) => commit('SET_SHOW_VIDEO', payload),
  },
  mutations: {
    SET_IS_LOADING: (state, payload) => { state.isLoading = payload.data },
    SET_IS_AUTH: (state, payload) => { state.isAuth = payload.data },
    SET_CHECK_TEMP: () => {},
    SET_PHOTO: (state, payload) => { state.photo = payload.data },
    SET_TEMP: (state, payload) => { state.temp = payload.data },
    SET_SHOW_VIDEO: (state, payload) => { state.showVideo = payload.data },
  },
  getters: {
    getIsLoading: state => state.isLoading,
    getIsAuth: state => state.isAuth,
    getPhoto: state => state.photo,
    getTemp: state => state.temp,
    getShowVideo: state => state.showVideo
  }
}
